.logo-div {
    display: flex;
    margin-top: 40px; }

.text-div {
    display: flex;
    flex-direction: row;
    font-size: 36px; }
.services-container {
    height: fit-content;
    background-color: black;
    display: flex;
    flex-direction: column;
    padding-bottom: 40px; }
.services-row {
    display: flex;
    justify-content: space-evenly;
    margin-top: 50px; }
.even {
    flex-direction: row; }
.odd {
    flex-direction: row-reverse; }
.services-div {
    height: 200px;
    width: 280px; }
.services-text {
    color: #B6B6B6;
    max-width: 200px;
    font-size: 20px; }
.read-more {
    margin-top: 10px;
    border: 1px solid #B6B6B6;
    background-color: #000;
    color: #B6B6B6; }
@media (max-width: 450px) {
    .services-row {
        justify-content: center;
        align-items: center;
        flex-direction: column; }
    .even {
        flex-direction: column; }
    .odd {
        flex-direction: column; }
    .text-div {
        font-size: 22px; }
    .logo-img {
        width: 100px;
        height: 100px; } }

@media (max-width: 590px) {
    .comment-form {
        flex-direction: column !important; } }

